@import url(https://fonts.googleapis.com/css2?family=Gowun+Batang:wght@700&family=Noto+Sans+KR:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contents{background:#F9F3DF;height:1200px;margin:0 0;padding-left:10px;padding-top:10px;font-size:12pt;font-family:'Noto Sans KR', sans-serif}

.titleBox{background:#364f6b;margin:0 0;height:100px;padding:20px}.titleBox .title{color:white;padding-top:20px;margin:0 0;font-size:32pt;font-family:'Gowun Batang', serif}

